.App {
  text-align: center;
  display: flex;
  flex-direction: row;
}
html {
  scroll-behavior: smooth;
}
/* The navigation bar */

.navi{
  display: flex;
  flex-direction: column;
  transition-duration: 500ms;
  margin: 0rem;
  padding: 0.5rem;
  color: white;
  background-color: rgb(30, 31, 43);
  position: fixed;
  top: 0;
  height: 100%;
  left: 0;
  border-right: solid 2px white;
}

.navi:hover {
  transition-duration: 500ms;
  margin: 0rem;
  padding: 1rem;
  color: rgb(106, 128, 255);
  background-color: rgb(11, 12, 20);
  top: 0;
  bottom: 0;
  left: 0;
  border-right: solid 4px rgb(203, 211, 255);
  font-size: 2rem;
}
/* Format of the component in the Navigation bar */
.navi h2{
  transition-duration: 500ms;
  padding-left: 0.5rem;
  text-align: left;
  margin-top: 1.5rem;
  margin-bottom: 1.5rem;
  font-size: 2rem;
  color: white;
}

a{
  text-decoration:none;
}

/* Effect of hovering the component in the Navigation bar */
.navi h2:hover {
  transition-duration: 500ms;
  color: rgb(106, 128, 255);
  background-color: rgb(58, 61, 83);
  margin-left: 1rem;
  margin-right: 0.5rem;
  margin-top: 2rem;
  margin-bottom: 2rem;
  border-right: solid 2px rgb(203, 211, 255);
}

.navi:hover h2 {
  transition-duration: 500ms;
  padding: 0;
  color: rgb(106, 128, 255);
  font-size: 2.5rem;
}

.navi .b {
  padding-left: 0;
  padding-right: 1rem;
  transition-duration: 500ms;
  margin: 0rem;
  padding-bottom: 0.8rem;
  font-size: 2rem;
  margin-bottom: 6rem;
}

.navi:hover .b {
  transition-duration: 500ms;
  padding-right: 15rem;
  padding-bottom: 1rem;
  color: rgb(106, 128, 255);
  font-size: 2.5rem;
  border-bottom: solid 2px;
}

.navi .b:hover {
  transition-duration: 500ms;
  color: rgb(106, 128, 255);
  background-color: rgb(58, 61, 83);
  margin-left: 1rem;
  margin-right: 0.5rem;
  margin-top: 0;
  margin-bottom: 6rem;
}

.Weather{
  position: absolute ;
  transition-duration: 500ms;
  width: 10px;
  opacity: 0;
  bottom: 0;
  margin-bottom: 2rem;
}

.navi:hover .Weather{
  transition-duration: 500ms;
  width: 50px;
  opacity: 1;
  bottom: 20px;
}

.navi .about:after {
  transition-duration: 300ms;
  opacity: 0;
  content: "bout";
  font-size: 0.1rem;
}

.navi:hover .about:after {
  transition-duration: 300ms;
  opacity: 1;
  content: "bout";
  font-size: 2rem;
  color: rgb(106, 128, 255);
}


.navi .exp:after {
  transition-duration: 300ms;
  opacity: 0;
  content: "xperience";
  font-size: 0.1rem;
}

.navi:hover .exp:after {
  transition-duration: 300ms;
  opacity: 1;
  content: "xperience";
  font-size: 2rem;
  color: rgb(106, 128, 255);
}


.navi .res:after {
  transition-duration: 300ms;
  opacity: 0;
  content: "esume";
  font-size: 0.1rem;
}

.navi:hover .res:after {
  transition-duration: 300ms;
  opacity: 1;
  content: "esume";
  font-size: 2rem;
  color: rgb(106, 128, 255);
}


.navi .con:after {
  transition-duration: 300ms;
  opacity: 0;
  content: "ontact";
  font-size: 0.1rem;
}

.navi:hover .con:after {
  transition-duration: 300ms;
  opacity: 1;
  content: "ontact";
  font-size: 2rem;
  color: rgb(106, 128, 255);
}


.App-header {
  text-align: center;

  flex:1;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: #f2f6ff;
  min-height: 100vh;
  font-size: calc(10px + 2vmin);
  color: rgb(14, 13, 13);
}
section{
  margin: 1rem;
  padding-bottom: 1rem;
  border-bottom: solid 2px black;
  font-weight: 700;
  font-size: 3rem;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu',
   'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;;
}

.name{
  margin: 0;
  padding-top: 2rem;
  font-size: 5rem;
  width: 100%;
  height: 10rem;
  color: white;
  background-color: #404b58;
  border-bottom: solid 2px rgb(194, 198, 248);
  animation: name-tag-color 5s infinite;
}
.page{
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}
.logo{
  width: 50%;
  height: auto;
  object-fit: contain;

}
.intro{
  flex:1;
  margin-left: 0;
  text-align: left;
  font-size: 2rem;
  font-family: 'Courier New', Courier, monospace;
  font-weight: 600;
}
.joke{
  width: 80%;
  height: 10rem;
  background-color: rgb(194, 198, 248);
}
.resume{
  width: 80%;
  height: auto;
  object-fit: contain;
}
.cpr{
  margin-top: 2rem;
  padding-bottom: 1rem;
  width: 100%;
  color: white;
  background-color: #404b58;
  font-size: 1rem;
  animation: name-tag-color 5s infinite;
}
.contact{
  margin-top: 1rem;
  padding-bottom: 1rem;
  border-bottom: solid 2px white;
  margin-bottom: 4rem;
  font-size: 3rem;
}
@media (prefers-reduced-motion: no-preference) {

}

@keyframes name-tag-color {
  50%{background-color: #1f2833;}
}
  
.App-link {
  color: #61dafb;
}


@media screen and (max-width: 720px) {
  .navi{
    display: flex;
    flex-direction: column;
    transition-duration: 500ms;
    margin: 0rem;
    padding: 0.5rem;
    padding-top: 0;
    color: white;
    background-color:rgba(53, 53, 53, 0);
    position: fixed;
    top: 0;
    height: 2.5rem;
    border: solid 0px white;
  }
  .navi:hover {
    transition-duration: 500ms;
    margin: 0rem;
    padding: 1rem;
    color: rgb(106, 128, 255);
    background-color: rgb(11, 12, 20);
    top: 0;
    bottom: 0;
    left: 0;
    border-right: solid 4px rgb(203, 211, 255);
    height: 100vh;
    font-size: 2rem;
    opacity: 1;
  }

  .navi h2{
    opacity: 0;
    font-size: 0.1rem;
    margin-top: 0;
    margin-bottom: 0;
  }

  .navi .b{
    opacity: 1;
    color: rgb(161, 160, 218);
    margin-bottom: 0rem;
    padding-bottom: 0;
    margin-top: 0;
  }
  .navi:hover .b{
    margin-bottom: 4rem;
    padding-bottom: 0.8rem;
    margin-top: 0;
  }
  .navi .b:hover {
    color: rgb(106, 128, 255);
    background-color: rgb(58, 61, 83);
    margin-left: 0;
    margin-right: 0;
    margin-top: 0;
  }
  .navi:hover h2{
    opacity: 1;
    margin-top: 1.5rem;
    margin-bottom: 1.5rem;
  }

  a{
    height: 0;
  }
  
  .navi:hover a{
    height:6rem;
  }

  .page{
    flex-direction: column;
  }
  .name{
    font-size: 3rem;
    height: 5rem;
    padding-bottom: 1rem;
  }
  .intro{
    margin-left: 1rem;
    margin-right: 0.5rem;
    font-size: 1.5rem;
  }

  .logo{
    width: 100%;
  }
}